.button {
    border-radius: 3px;
    padding: 5px 24px;
    color: white;
}

.disabled {
    background: #004D99;
    opacity: 0.5;
}

.secondary {
    color: #0066CC;
    background: white;
    border-color: #0066CC;
}

.default {
    background: #0066CC;
}

.red {
    background: #CC1439;
}

.default:hover {
    background: #0080FF;
}

.red:hover {
    background: #FF1A47;
}

.secondary:hover {
    background: #E5F0FA;
}


.default:active {
background: #0059B2;
}

.secondary:active {
    background: #0059B2;
}

.red:active {
    background: #B31232;
}

.button:disabled {
    background: #004D99;
    opacity: 0.5;
}
